export default "import React from 'react';\nimport {ResponsiveContainer, Treemap} from 'recharts';\nimport data from './data';\n\nconst SimpleTreemap = () => (\n  <ResponsiveContainer width='100%' height={200}>\n    <Treemap\n      data={data}\n      dataKey='size'\n      ratio={4 / 3}\n      stroke='#fff'\n      fill='#4299E1'\n    />\n  </ResponsiveContainer>\n);\n\nexport default SimpleTreemap;\n";