export default "import React from 'react';\nimport {\n  CartesianGrid,\n  Legend,\n  Line,\n  LineChart,\n  ResponsiveContainer,\n  Tooltip,\n  XAxis,\n  YAxis,\n} from 'recharts';\nimport data from './data';\n\nconst LineChartWithXAxisPading = () => (\n  <ResponsiveContainer width='100%' height={200}>\n    <LineChart data={data} margin={{top: 10, right: 0, left: -25, bottom: 0}}>\n      <XAxis dataKey='name' padding={{left: 30, right: 30}} />\n      <YAxis />\n      <CartesianGrid strokeDasharray='3 3' />\n      <Tooltip />\n      <Legend />\n      <Line type='monotone' dataKey='pv' stroke='#4299E1' activeDot={{r: 8}} />\n      <Line type='monotone' dataKey='uv' stroke='#F04F47' />\n    </LineChart>\n  </ResponsiveContainer>\n);\n\nexport default LineChartWithXAxisPading;\n";