export default "import React from 'react';\nimport {\n  Area,\n  AreaChart,\n  CartesianGrid,\n  ResponsiveContainer,\n  Tooltip,\n  XAxis,\n  YAxis,\n} from 'recharts';\nimport Box from '@mui/material/Box';\nimport {useThemeContext} from '../../../../../@crema/utility/AppContextProvider/ThemeContextProvider';\n\nconst data = [\n  {name: 'Page A', uv: 4000},\n  {name: 'Page B', uv: 3000},\n  {name: 'Page C', uv: 2000},\n  {name: 'Page D'},\n  {name: 'Page E', uv: 1890},\n  {name: 'Page F', uv: 2390},\n  {name: 'Page G', uv: 3490},\n];\nconst AreaChartConnectNulls = () => {\n  const {theme} = useThemeContext();\n  return (\n    <ResponsiveContainer width='100%'>\n      <div>\n        <Box mb={5}>\n          <ResponsiveContainer width='100%' height={200}>\n            <AreaChart\n              data={data}\n              margin={{top: 10, right: 0, left: -25, bottom: 0}}\n            >\n              <XAxis dataKey='name' />\n              <YAxis />\n              <CartesianGrid strokeDasharray='3 3' />\n              <Tooltip />\n              <Area\n                type='monotone'\n                dataKey='uv'\n                fill={theme.palette.primary.main}\n              />\n            </AreaChart>\n          </ResponsiveContainer>\n        </Box>\n        <Box mb={4}>\n          <ResponsiveContainer width='100%' height={200}>\n            <AreaChart\n              data={data}\n              margin={{top: 10, right: 0, left: -25, bottom: 0}}\n            >\n              <XAxis dataKey='name' />\n              <YAxis />\n              <CartesianGrid strokeDasharray='3 3' />\n              <Tooltip />\n              <Area\n                connectNulls={true}\n                type='monotone'\n                dataKey='uv'\n                stroke={theme.palette.secondary.main}\n                fill={theme.palette.secondary.main}\n              />\n            </AreaChart>\n          </ResponsiveContainer>\n        </Box>\n      </div>\n    </ResponsiveContainer>\n  );\n};\n\nexport default AreaChartConnectNulls;\n";