export default "import React, {useEffect, useState} from 'react';\nimport UploadModern from '../components/UploadModern';\nimport {AppGrid} from '../../../../@crema';\nimport {useDropzone} from 'react-dropzone';\nimport PreviewThumb from '../components/PreviewThumb';\n\nconst Previews = () => {\n  const [uploadedFiles, setUploadedFiles] = useState([]);\n  const dropzone = useDropzone({\n    accept: 'image/*',\n    onDrop: (acceptedFiles) => {\n      setUploadedFiles(\n        acceptedFiles.map((file) =>\n          Object.assign(file, {\n            preview: URL.createObjectURL(file),\n          }),\n        ),\n      );\n    },\n  });\n  useEffect(() => {\n    setUploadedFiles(dropzone.acceptedFiles);\n  }, [dropzone.acceptedFiles]);\n\n  const onDeleteUploadFile = (file) => {\n    dropzone.acceptedFiles.splice(dropzone.acceptedFiles.indexOf(file), 1);\n    setUploadedFiles([...dropzone.acceptedFiles]);\n  };\n\n  return (\n    <section className='container' style={{cursor: 'pointer'}}>\n      <UploadModern\n        uploadText='Drag n drop some files here, or click to select files'\n        setUploadedFiles={setUploadedFiles}\n        dropzone={dropzone}\n      />\n      <AppGrid\n        sx={{\n          maxWidth: 500,\n        }}\n        data={uploadedFiles}\n        column={4}\n        itemPadding={5}\n        renderRow={(file, index) => (\n          <PreviewThumb\n            file={file}\n            onDeleteUploadFile={onDeleteUploadFile}\n            key={index + file.path}\n          />\n        )}\n      />\n    </section>\n  );\n};\n\nexport default Previews;\n";