export default "import React from 'react';\nimport {store} from 'react-notifications-component';\nimport Button from '@mui/material/Button';\n\nimport {getContainer, getMessage, getType} from '../helpers/randomize';\nimport notification from '../helpers/notification';\nimport Box from '@mui/material/Box';\n\nconst AnimationExit = () => {\n  const add = (htmlClasses) => {\n    const type = getType();\n    return store.addNotification(\n      Object.assign({}, notification, {\n        slidingExit: {delay: 300},\n        animationOut: htmlClasses,\n        container: getContainer(),\n        message: getMessage(type),\n        type,\n      }),\n    );\n  };\n\n  return (\n    <Box display='flex' flexWrap='wrap'>\n      <Box mr={2} my={1}>\n        <Button\n          color='primary'\n          variant='contained'\n          onClick={() => add(['animated bounceOut'])}\n        >\n          Bounce Out\n        </Button>\n      </Box>\n      <Box mr={2} my={1}>\n        <Button\n          color='primary'\n          variant='contained'\n          onClick={() => add(['animated fadeOut'])}\n        >\n          Fade Out\n        </Button>\n      </Box>\n      <Box mr={2} my={1}>\n        <Button\n          color='primary'\n          variant='contained'\n          onClick={() => add(['animated flipOutX'])}\n        >\n          Flip Out X\n        </Button>\n      </Box>\n      <Box mr={2} my={1}>\n        <Button\n          color='primary'\n          variant='contained'\n          onClick={() => add(['animated flipOutY'])}\n        >\n          Flip Out Y\n        </Button>\n      </Box>\n      <Box mr={2} my={1}>\n        <Button\n          color='primary'\n          variant='contained'\n          onClick={() => add(['animated zoomOut'])}\n        >\n          Zoom Out\n        </Button>\n      </Box>\n    </Box>\n  );\n};\nexport default AnimationExit;\n";