export default "import React from 'react';\nimport {store} from 'react-notifications-component';\nimport Button from '@mui/material/Button';\n\nimport notification from '../helpers/notification';\nimport {getMessage, getTitle, getType} from '../helpers/randomize';\nimport Box from '@mui/material/Box';\n\nexport default class ContainerExample extends React.Component {\n  add = (container) => {\n    const type = getType();\n\n    return store.addNotification(\n      Object.assign({}, notification, {\n        title: getTitle(type),\n        message: getMessage(type),\n        container,\n        type,\n      }),\n    );\n  };\n\n  render() {\n    return (\n      <Box display='flex' flexWrap='wrap'>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('top-left')}\n          >\n            Top Left\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('top-right')}\n          >\n            Top Right\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('top-center')}\n          >\n            Top Center\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('bottom-left')}\n          >\n            Bottom Left\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('bottom-right')}\n          >\n            Bottom Right\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('bottom-center')}\n          >\n            Bottom Center\n          </Button>\n        </Box>\n      </Box>\n    );\n  }\n}\n";