export default "import React from 'react';\nimport {\n  CartesianGrid,\n  Line,\n  LineChart,\n  ResponsiveContainer,\n  Tooltip,\n  XAxis,\n  YAxis,\n} from 'recharts';\nimport data from './data';\nimport Box from '@mui/material/Box';\n\nconst LineChartConnectNulls = () => (\n  <ResponsiveContainer width='100%' height={600}>\n    <div>\n      <Box mb={4}>\n        <ResponsiveContainer width='100%' height={200}>\n          <LineChart\n            data={data}\n            margin={{top: 10, right: 0, left: -25, bottom: 0}}\n          >\n            <XAxis dataKey='name' />\n            <YAxis />\n            <CartesianGrid strokeDasharray='3 3' />\n            <Tooltip />\n            <Line\n              type='monotone'\n              dataKey='uv'\n              stroke='#4299E1'\n              fill='#4299E1'\n            />\n          </LineChart>\n        </ResponsiveContainer>\n      </Box>\n      <Box mb={4}>\n        <ResponsiveContainer width='100%' height={200}>\n          <LineChart\n            data={data}\n            margin={{top: 10, right: 0, left: -25, bottom: 0}}\n          >\n            <XAxis dataKey='name' />\n            <YAxis />\n            <CartesianGrid strokeDasharray='3 3' />\n            <Tooltip />\n            <Line\n              connectNulls={true}\n              type='monotone'\n              dataKey='uv'\n              stroke='#4299E1'\n              fill='#4299E1'\n            />\n          </LineChart>\n        </ResponsiveContainer>\n      </Box>\n    </div>\n  </ResponsiveContainer>\n);\n\nexport default LineChartConnectNulls;\n";