export default "import React from 'react';\nimport {store} from 'react-notifications-component';\nimport Button from '@mui/material/Button';\n\nimport notification from '../helpers/notification';\nimport {getContainer, getMessage, getTitle} from '../helpers/randomize';\nimport Box from '@mui/material/Box';\n\nexport default class TypeExample extends React.Component {\n  add = (type) => {\n    return store.addNotification(\n      Object.assign({}, notification, {\n        type,\n        title: getTitle(type),\n        message: getMessage(type),\n        container: getContainer(),\n      }),\n    );\n  };\n\n  render() {\n    return (\n      <Box display='flex' flexWrap='wrap'>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('success')}\n          >\n            Success\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('default')}\n          >\n            Default\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('warning')}\n          >\n            Warning\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('info')}\n          >\n            Info\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('danger')}\n          >\n            Danger\n          </Button>\n        </Box>\n        <Box mr={2} my={1}>\n          <Button\n            color='primary'\n            variant='contained'\n            onClick={() => this.add('awesome')}\n          >\n            Custom\n          </Button>\n        </Box>\n      </Box>\n    );\n  }\n}\n";