import {
  ADD_CART_ITEM,
  FETCH_ERROR,
  FETCH_START,
  FETCH_SUCCESS,
  GET_CUSTOMERS,
  GET_ECOMMERCE_LIST,
  GET_RECENT_ORDER,
  REMOVE_CART_ITEM,
  SET_FILTER_DATA,
  SET_PRODUCT_DATA,
  SET_PRODUCT_VIEW_TYPE,
  UPDATE_CART_ITEM,
  // SET_SearchFILTER_DATA,
  GET_EMPlOYEES,
  GET_OLDEMPlOYEES,
  GET_COUPON,
  GET_RUNING_COST,
  GET_DELRUNING_COST,
  SET_CART_ITEMS,
  GET_SALEPOINT,
  GET_OLDSALEPOINT,
} from '../../shared/constants/ActionTypes';
import jwtAxios from '@crema/services/auth/jwt-auth';

export const onGetEcommerceData = (filterData) => {
  return (dispatch) => {
    dispatch({type: FETCH_START});
    jwtAxios
      .get('/api/ecommerce/list', {
        params: {filterData},
      })
      .then((data) => {
        if (data.status === 200) {
          console.log('ecommerceList: ', data);
          dispatch({type: FETCH_SUCCESS});
          dispatch({type: GET_ECOMMERCE_LIST, payload: data.data});
        } else {
          dispatch({
            type: FETCH_ERROR,
            payload: 'Something went wrong, Please try again!',
          });
        }
      })
      .catch((error) => {
        dispatch({type: FETCH_ERROR, payload: error.message});
      });
  };
};
