async function updateOrder(
  order,
  orderID,
  orderPrice,
  orderStatus,
  odldStatus,
  BilingInformtio,
) {
  let cost = 0;
  order.CartItem = order.CartItem.filter((item) => item.qty > 0);

  await order.CartItem.reduce(async function (sum, item) {
    await firebase
      .firestore()
      .collection('products')
      .doc(item.id)
      .get()
      .then((i) => {
        let temp = i.data().cost;
        cost += temp * item.qty;
      });
    // set initial value as 0
  }, 0);

  order.price = orderPrice.Price;
  order.discouontValue = orderPrice.discouontValue;
  order.status = orderStatus;
  order.totalPrice = orderPrice.totalOrderProce;
  order.shippingValue = orderPrice.shippingValue;
  order.cost = cost;
  order.BilingInformtion = BilingInformtio;
  console.log(order);
  firebase
    .firestore()
    .collection('Orders')
    .doc(orderID)
    .set(order)

    .then(async () => {
      alert('Update Order Done: :) ');
      if (orderStatus != odldStatus) {
        let length = await firebase
          .firestore()
          .collection('databaseCount')
          .doc('orderCount')
          .get();

        let orderFild = '';
        if (orderStatus === 'Pending') orderFild = 'Pending';
        else if (orderStatus === 'In Progress') orderFild = 'InProgress';
        else if (orderStatus === 'In Delivery') orderFild = 'InDelivery';
        else if (orderStatus === 'Completed') orderFild = 'Completed';
        else if (orderStatus === 'Canceled') orderFild = 'Canceled';
        let oldOrderFild = '';
        if (odldStatus === 'Pending') oldOrderFild = 'Pending';
        else if (odldStatus === 'In Progress') oldOrderFild = 'InProgress';
        else if (odldStatus === 'In Delivery') oldOrderFild = 'InDelivery';
        else if (odldStatus === 'Completed') oldOrderFild = 'Completed';
        else if (odldStatus === 'Canceled') oldOrderFild = 'Canceled';
        length.data()[oldOrderFild] -= 1;
        length.data()[orderFild] += 1;
        firebase
          .firestore()
          .collection('databaseCount')
          .doc('orderCount')
          .set(length);
      }
    });
}
